import { useContext, useEffect, useState } from "react";
import PreloadContext from "../context/PreloadContext";
import { getChalangeAsync } from "../services/chalange";
import { delay } from "../util";
import { chalangeTypes } from "../util/constants";
const defaultState = {
  msg: "",
  chalangeType: null,
  x1: 0,
  x2: 0,
  y1: 0,
  y2: 0,
};
export default (stepper) => {
  const preloader = useContext(PreloadContext);
  const [state, setState] = useState(defaultState);

  useEffect(() => {
    if (stepper.step === 0) {
      setState((state) => ({
        ...state,
        chalangeType: chalangeTypes.ELIPSE,
        msg: "Posicione sua face no quadrante",
      }));
    }

    if (stepper.step === 1) {
      getChalange();
      setState((state) => ({
        ...state,
        chalangeType: chalangeTypes.NOISE_SQUARE,
      }));
    }
  }, [stepper.step]);

  const getChalange = async () => {
    setState({
      ...state,
      x1: 0,
      x2: 0,
      y1: 0,
      y2: 0,
      msg: "",
    });

    preloader.setLoading(true);
    await delay(3000);

    preloader.setLoading(false);

    const x1 = Math.ceil(Math.random() * 100);
    setState((state) => ({
      ...state,
      x1,
      y1: x1 + 10,
      x2: x1 + 10,
      y2: x1 + 20,
      msg: "Posicione seu nariz no quadrado vermelho",
    }));

    // getChalangeAsync()
    //   .then((resut) => {})
    //   .catch((err) => {});
  };

  return { ...state, getChalange: getChalange };
};

