import { sendChalangeAsync } from "../services/chalange";
import { getFileFromVideo } from "../util/video";
import useAlert from "./useAlert";
import useTries from "./useTries";

export default (video, stepper, chalange, history) => {
  const alert = useAlert();
  const tryAgain = () =>
    alert.setData({
      open: true,
      title: "Alguma coisa deu errado!",
      message: "Não conseguimos reconhecer sua prova de vida",
      buttonText: "Tentar Novamente",
      onClick: () => {
        alert.close();
        video.play();
        chalange.getChalange();
      },
    });

  const giveUp = () =>
    alert.setData({
      open: true,
      title: "Alguma coisa deu errado!",
      message:
        "Não conseguinos, sem problemas vamos avançar e depois tentaremos novamente",
      buttonText: "Proxima Etapa",
      onClick: () => {
        alert.close();
        video.play();
        history.push("/conclusao");
      },
    });

  const retry = useTries(3, tryAgain, giveUp);

  const send = async () => {
    video.pause();
    const file = getFileFromVideo(video);

    const formData = new FormData();
    formData.append("file", file);

    try {
      await sendChalangeAsync(formData);
      video.play();
      stepper.next();
    } catch (error) {
      retry();
    }
  };

  return send;
};
