import { base64ToFile } from ".";

function getUserMedia(constraints) {
  // if Promise-based API is available, use it
  if (navigator.mediaDevices) {
    return navigator.mediaDevices.getUserMedia(constraints);
  }

  // otherwise try falling back to old, possibly prefixed API...
  var legacyApi =
    navigator.getUserMedia ||
    navigator.webkitGetUserMedia ||
    navigator.mozGetUserMedia ||
    navigator.msGetUserMedia;

  if (legacyApi) {
    // ...and promisify it
    return new Promise(function (resolve, reject) {
      legacyApi.bind(navigator)(constraints, resolve, reject);
    });
  }
}

/**
 * @returns Promise<any>
 */
export function getStream() {
  if (
    !navigator.mediaDevices &&
    !navigator.getUserMedia &&
    !navigator.webkitGetUserMedia &&
    !navigator.mozGetUserMedia &&
    !navigator.msGetUserMedia
  ) {
    alert("User Media API not supported.");
    return;
  }

  var constraints = {};
  constraints["video"] = true;

  return getUserMedia(constraints);
}

export function setElementStream(element, stream) {
  if ("srcObject" in element) {
    element.srcObject = stream;
  } else if (navigator.mozGetUserMedia) {
    element.mozSrcObject = stream;
  } else {
    element.src = (window.URL || window.webkitURL).createObjectURL(stream);
  }
  element.play();
}

export const getFileFromVideo = (video) => {
  const canvas = document.createElement("canvas");
  canvas.width = video.style.width.replace("px", "");
  canvas.height = video.style.height.replace("px", "");
  const context = canvas.getContext("2d");
  context.drawImage(video, 0, 0, canvas.width, canvas.height);
  var data = canvas.toDataURL("image/png");
  const file = base64ToFile(data, "foto.png");
  return file;
};
