import { useState } from "react";

//useTries
export default (totalTries, tryAgain, giveUp) => {
  const [tries, setTries] = useState(0);

  return (...params) => {
    if (tries < totalTries - 1) {
      setTries(tries + 1);
      tryAgain(...params);
    } else {
      giveUp(...params);
    }
  };
};
