import React, { useContext, useEffect } from "react";
import StreamContext from "../../context/StreamContext";
import { getVideoRef, setVideoRef } from "../../util/refs";
import { setElementStream } from "../../util/video";

const Video = ({ style }) => {
  const { stream } = useContext(StreamContext);

  useEffect(() => {
    if (stream) {
      setElementStream(getVideoRef(), stream);
    }
  }, [stream]);

  return (
    <video
      ref={(x) => setVideoRef(x)}
      // width={width}
      // height={height}
      style={style}
    ></video>
  );
};

export default Video;
