import { Button, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import Container70 from "../../components/Container70";
import StepperProvaDeVida from "../../components/StepperProvaDeVida";
import {
  alignCenter,
  flex,
  flexColumn,
  flexRow,
  justifyCenter,
  justifySpapceAround,
} from "../../jss";
import Logo from "./Logo";

const useStyle = makeStyles((theme) => {
  return {
    root: {
      width: "100%",
      backgroundColor: "#fff",
      ...flex,
      ...flexColumn,
    },
    subroot: {
      height: "100%",
      ...flex,
      ...flexColumn,
      ...justifySpapceAround,
      paddingRight: "2.5rem",
      paddingLeft: "2.5rem",
    },
    logoWrapper: {
      width: "100%",
      ...flex,
      ...flexRow,
      ...justifyCenter,
      ...alignCenter,
    },
    content: {
      color: theme.palette.secondary.contrastText,
      width: "100%",
      ...flex,
      ...flexColumn,
      ...justifySpapceAround,
      minHeight: 144,
    },
    parag: {
      lineHeight: 1.7,
      marginBottom: 10,
    },
  };
});

const End = () => {
  const classes = useStyle();
  const backWa = () => {};

  return (
    <Container70
      content={
        <div className={classes.root}>
          <StepperProvaDeVida activeStep={2} />

          <div className={classes.subroot}>
            <div className={classes.logoWrapper}>
              <Logo />
            </div>

            <div className={classes.content}>
              <Typography
                className={classes.parag}
                variant="body1"
                color="initial"
              >
                Seu processo foi concluído com sucesso!
              </Typography>
              <Typography
                className={classes.parag}
                variant="body1"
                color="initial"
              >
                Aguarde nosso contato pelo WhatsApp
              </Typography>

              <Typography
                className={classes.parag}
                variant="body1"
                color="initial"
              >
                Safra Consignado agradece!
              </Typography>
            </div>
          </div>
        </div>
      }
      footer={
        <Button variant="contained" color="primary" onClick={backWa}>
          Voltar ao Whatsapp
        </Button>
      }
    ></Container70>
  );
};

export default End;
