import React from "react";

const Square = ({ className, x1, x2, y1, y2, width, height }) => {
  const top = height * (y1 / 100);
  const left = width * (x1 / 100);
  const _height = height * (y2 / 100) - top;
  const _width = width * (x2 / 100) - left;

  return (
    <div
      className={className}
      style={{
        top,
        left,
        width: _width,
        height: _height,
      }}
    />
  );
};

export default Square;
