import { Button, makeStyles } from "@material-ui/core";
import React, { useContext, useEffect } from "react";
import Container70 from "../../components/Container70";
import StepperProvaDeVida from "../../components/StepperProvaDeVida";
import VideoContainer from "../../components/VideoContainer";
import StreamContext from "../../context/StreamContext";
import useActiveStep from "../../hooks/useActiveStep";
import useGetChalange from "../../hooks/useGetChalange";
import useSendChalange from "../../hooks/useSendChalange";
import useSendSelfie from "../../hooks/useSendSelfie";
import {
  alignCenter,
  flex,
  flexColumn,
  justifyCenter,
  justifySpapceAround,
} from "../../jss";
import { getVideoRef } from "../../util/refs";

const useStyle = makeStyles((theme) => {
  return {
    root: {
      width: "100%",
      backgroundColor: theme.palette.background.default,
      ...flex,
      ...flexColumn,
    },
    innerFooter: {
      width: "100%",
      height: "100%",
      ...flex,
      ...flexColumn,
      ...alignCenter,
      ...justifySpapceAround,
    },
    msg: {},
  };
});

const Self = (props) => {
  const video = getVideoRef();
  const classes = useStyle();
  const stepper = useActiveStep();
  const chalange = useGetChalange(stepper);
  const sendSelfie = useSendSelfie(video, stepper);
  const sendChalange = useSendChalange(video, stepper, chalange, props.history);

  const send = async () => {
    if (stepper.step === 0) {
      sendSelfie();
    }
    if (stepper.step === 1) {
      sendChalange();
    }
  };

  const { stream } = useContext(StreamContext);
  useEffect(() => {
    if (!stream) props.history.replace("/");
  }, []);

  return (
    <Container70
      content={
        <div className={classes.root}>
          <StepperProvaDeVida activeStep={stepper.step} />
          <VideoContainer {...chalange} />
        </div>
      }
      footer={
        <div className={classes.innerFooter}>
          <div className={classes.msg}>{chalange.msg}</div>
          <Button variant="contained" color="primary" onClick={send}>
            Capturar
          </Button>
        </div>
      }
    ></Container70>
  );
};

export default Self;
