import axios from "axios";
import { BASE_URL } from "../configuration";

export const sendChalangeAsync = async (data) => {
  return await axios.post(`${BASE_URL}/send-file`, data, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};

export const getChalangeAsync = async () => {
  return await axios.get(`${BASE_URL}/`);
};
